<template>
    <div>
        <van-nav-bar
	        title="登录"
	        
	        left-arrow 
          @click-left="$router.go(-1)"
	    />
        <!-- 网站logo -->
        <div class="logo">
            <img src="../assets/images/logo.png" />
        </div>

        <!-- form表单 -->
    <van-form @submit="login">
      <van-field type="text" label="手机号" placeholder="手机号" v-model="userInfo.phone">
        <template v-slot:left-icon>
          <i class="fa fa-phone fa-lg" aria-hidden="true"></i>
        </template>
      </van-field>

      <!-- 密码 -->
      <van-field
        :type="ishas?'password':'text'"
        label="密码"
        placeholder="密码"
        v-model="userInfo.password"
        @click-right-icon="viewPassword"
      >
        <template v-slot:left-icon>
          <i class="fa fa-unlock-alt fa-lg" aria-hidden="true"></i>
        </template>
        <template v-slot:right-icon>
          <i class="fa fa-lg" :class="ishas?'fa-eye-slash':'fa-eye'" aria-hidden="true"></i>
          <!-- fa-eye-slash -->
        </template>
      </van-field>
      
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">
          <!-- " -->
          登录
        </van-button>
      </div>
    </van-form>
    
    <!-- 注册/忘记密码 -->
    <div class="rf_box clearfix">
        <div class="register fl" @click="register">注册</div>
        <div class="forgetPassword fr" @click="backPassword">忘记密码</div>
    </div>
    </div>
</template>

<script>
//导入验证表单模块
  import validForm from '../assets/js/validForm'
    export default {
        data() {
      return {

        //用户注册信息
        userInfo: {
          phone: '',
          password: ''
        },
        ishas: true,
      }
    },
    methods:{
            //切换密码的格式
            viewPassword() {
                this.ishas = !this.ishas;
            },
            //跳转注册页面
            register(){
                this.$router.push({name:'register'})
            },

            //登录
          login() {

            //进行验证表单
            if (validForm.validUserForm(this.userInfo)) {

              //复制对象
              let data = Object.assign({}, this.userInfo);

              //请求密钥
              data.appkey = this.appkey;

              //开启加载提示
              this.$toast.loading({
                message: '加载中...',
                forbidClick: true,

                //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                duration:0
              });

              //发起注册请求
              this.axios({
                method: 'POST',
                url: '/login',

                //post传递参数需要写在data
                data
              }).then(result => {
                //关闭加载提示
                this.$toast.clear();
                
                if (result.data.code == 200) {
                  //跳转到菜单
                  
                  //修改my页面的更改背景&登录显隐
                  localStorage.setItem('isloginCbg',true)

                  //跳转到菜单页
                  // this.$router.go(-2)
                  this.$router.push({name:'menu'})

                  //存储token，用于后面登录验证
                  localStorage.setItem('__Tk', result.data.token);
                  
                } else {
                  this.$toast(result.data.msg);
                }
              }).catch(() => {
                //关闭加载提示
                this.$toast.clear();
                
              })
            }

          },

          //跳转忘记密码
          backPassword(){
            this.$router.push({name:'verCode'})
          }

            
        }
    }
</script>

<style lang="less" scoped>
@c: #ff8800;
.logo {
  width: 100px;
  height: 100px;
  margin: 60px auto;
}
//更改register导航颜色
/deep/ .van-nav-bar__text {
  color: @c;
}
/deep/ .van-icon-arrow-left {
  color: @c;
}
//更改提交按钮颜色
.van-button {
  background-color: @c;
  border: none;
}
//注册/忘记密码
.rf_box{
    padding: 0 16px;
    // .register{

    // }
    // .forgetPassword{

    // }
}
</style>